import {ProfileType} from "../../../types/types";
import React from "react";
import CardContent from "@material-ui/core/CardContent/CardContent";
import Typography from "@material-ui/core/Typography";
import CardActions from "@material-ui/core/CardActions";
import Fab from "@material-ui/core/Fab/Fab";
import EditIcon from '@material-ui/icons/Edit';
import Paper from "@material-ui/core/Paper";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import {Grid} from "@material-ui/core";

type ProfileDataPropsType = {
    profile: ProfileType
    isOwner: boolean
    edithMode: () => void
    infoUser: string

}


const ProfileData: React.FC<ProfileDataPropsType> = ({infoUser, profile, edithMode, isOwner}) => {

    const ObjKeysProfileContacts = profile.contacts

    return (
        <Paper variant="outlined" elevation={3}>
            <Grid container className={infoUser}>
                <Grid item xs={6}>
                    <CardContent>
                        <Typography variant={"h6"} color={"primary"}>
                            AboutMe:
                        </Typography>
                        <Typography variant={"body2"}>
                            {profile.aboutMe}
                        </Typography>
                        <Typography variant={"h6"} color={"primary"}>
                            Contacts:
                        </Typography>
                        <List>
                            {(Object.keys(ObjKeysProfileContacts) as Array<keyof typeof ObjKeysProfileContacts>).map((el, id) => {
                                return (
                                    <ListItem key={`${id}_${el}`}><b>{el}:</b>
                                        {ObjKeysProfileContacts[el] !== null && ObjKeysProfileContacts[el] !== undefined ? ObjKeysProfileContacts[el] : "...."}
                                    </ListItem>
                                )
                            })}
                        </List>
                        <Typography variant={"h6"} color={"primary"}>
                            Looking for a job:
                        </Typography>
                        <Typography variant={"body2"}>
                            {profile?.lookingForAJob ? <i>Yes</i> : <i>No</i>}
                        </Typography>
                        <Typography variant={"h6"} color={"primary"}>
                            Explanation:
                        </Typography>
                        <Typography variant={"body2"}>
                            {profile?.lookingForAJobDescription
                                ? profile.lookingForAJobDescription
                                : "..."}
                        </Typography>
                    </CardContent>
                </Grid>
                <Grid container item xs={6}>
                    <Grid item xs={5}></Grid>
                    <Grid item xs={7}>
                        <CardActions>
                            {isOwner &&
                            <Fab
                                variant={"extended"}
                                size={"small"}
                                onClick={edithMode}
                                color={"default"}
                                aria-label="edit">
                                <EditIcon/>
                            </Fab>
                            }
                        </CardActions>
                    </Grid>
                </Grid>
            </Grid>
        </Paper>

    )
}

export default ProfileData;
